<template>
	<view>
		<view class="header" :style="{'height':titleBarHeight,'padding-top':statusBarHeight,'background-color': nav.bg}">
			<view class="header-back" v-if="nav.isBack" @tap="back">
				
			</view>
			<view class="header-title">{{nav.navTitle}}</view>
			<view class="header-right">
				<slot name="right-text"></slot>
				<slot name="right-icons"></slot>
			</view>
		</view>
	</view>
</template>
<script>
	import { uniIcons } from "@dcloudio/uni-ui"
	export default {
		props: {
			nav: {
				type: Object,
				default: {}
			},
		},
		data() {
			return {
				statusBarHeight: 0,
				titleBarHeight: 0
			}
		},
		created() {
			let that = this;
			uni.getSystemInfo({
				success: function(res) {
					if (res.model.indexOf('iPhone') !== -1) {
						that.titleBarHeight = 44 + 'px';
					} else {
						that.titleBarHeight = 48 + 'px';
					}
					that.statusBarHeight = res.statusBarHeight + 'px'
				}
			})
		},
		methods: {
			back() { // 回到上一页
				uni.navigateBack()
			}
		}
	}
</script>

<style lang="scss" scoped>
	.header {
		display: flex;
		align-items: center;
		width: 100%;
		display: flex;
		flex-direction: row;
		.header-back {
			padding: 10rpx;
		}
		.header-title {
			flex: 1;
			font-size: 32rpx;
			color: $fontWhiteColor;
			font-weight: 700;
			text-align: center;
			/* #ifdef MP-WEIXIN */
			text-align: left;
			/* #endif */
			@include ellipsis()
		}
		
	}
</style>
